@import "@/styles/bootstrap.scss";

.mapViewer {
    @extend %right-column;

    @include respond-to(null, "lg") {
        display: none;
    }

    border-radius: 15px;
    overflow: hidden;
    flex: auto;
    border: 1px solid #29292e;
    background-color: #29292e;
    position: relative;
    z-index: 0;
}
